body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.card-img-top {
  width: 27.3vw;
  height: 20vw;
  object-fit: contain;
}
.post-modal {
  display: block; 
  width: 1500px; 
  padding-left: 0px;
}
.upload-box{
  text-align:center;
  padding-top: 15%;
}
.card-title {
  display: flex;
  justify-content: space-between;
}
.component-search-input {
  border-bottom: 0px solid #ccc;
}

.component-search-input > div {
  margin: 8px 8px 8px 0px;
}

.component-search-input input {
  border-radius: 4px;
  border: 1px solid #bbb;
  box-sizing: border-box;
  font-size: 18px;
  padding: 10px 8px;
  width: 100%;
  background-color: transparent;
}

html,
body {
  margin: 0;
}

#App {
  font-family: sans-serif;

  /* Give app full page to work with */
  height: 100vh;
}

#page-wrap {
  text-align: center;

  /* Prevent sidebar from showing a scrollbar on page */
  overflow: auto;
}

/* Individual item */
.bm-item {
  display: inline-block;

  /* Our sidebar item styling */
  text-decoration: none;
  margin-bottom: 10px;
  color: #d1d1d1;
  transition: color 0.2s;
}

/* Change color on hover */
.bm-item:hover {
  color: white;
}

/* The rest copied directly from react-burger-menu docs */

/* Position and sizing of burger button */
.bm-burger-button {
  position: fixed;
  width: 36px;
  height: 30px;
  left: 36px;
  top: 36px;
}

/* Color/shape of burger icon bars */
.bm-burger-bars {
  background: #373a47;
}

/* Position and sizing of clickable cross button */
.bm-cross-button {
  height: 24px;
  width: 24px;
}

/* Color/shape of close button cross */
.bm-cross {
  background: #bdc3c7;
}

/* General sidebar styles */
.bm-menu {
  background: darkslateblue;;
  padding: 2.5em 1.5em 0;
  font-size: 1.15em;
}

/* Morph shape necessary with bubble or elastic */
.bm-morph-shape {
  fill: #373a47;
}

/* Wrapper for item list */
.bm-item-list {
  color: #b8b7ad;
}

/* Styling of overlay */
.bm-overlay {
  background: rgba(0, 0, 0, 0.3);
}

.row-header {
    margin: auto;
    padding: auto;
}

.row-content {
    margin: auto;
    padding: auto;
    border-bottom: 1px ridge;
    min-height: 400px;
}

.footer {
    margin: auto;
    padding: 20px 0px 20px 0px;
}
.jumbotron {
    padding: 100px 30px 100px 30px;
    margin: auto;
    background-color: white;
}

address {
    font-size: 80%;
    color: #0f0f0f;
}

.navbar-dark {
    background-color: darkslateblue;
}

.login-div {
    width: 500px;
    height: 500px;
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;

    margin: auto;
}

.logo-div {
    padding-bottom: 10%;
}

.nav-buttons {
    color: lightgray;
}

.nav-link {
    color: lightgray;
}

nav a:hover {
    color: white;
}

.dropzone {
    position: relative;
    width: 100%;
    height: 200px;
    border-width: 2px;
    border-color: rgb(102, 102, 102);
    border-style: dashed;
    border-radius: 5px;
}

.item-button {
  margin: 2px;
}

.start-of-home {
    padding-top: 100px;
}
